import React, { useRef, useEffect, useCallback } from 'react';
import { useSpring, animated } from 'react-spring';
import styled from 'styled-components';
import { MdClose } from 'react-icons/md';
import { Image } from "antd";
import { SpinnerDotted } from 'spinners-react';

const Background = styled.div`
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  // background: #1B1B1B
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const ModalWrapper = styled.div`
  width: 500px;
  height: 350px;
  box-shadow: 0 5px 16px rgba(0, 0, 0, 0.2);
  background: #fff;
  // background: #1B1B1B

  color: #000;
  // display: grid;
  // grid-template-rows: 1fr 1fr;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 10;
  border-radius: 10px;
`;

const ModalImg = styled.img`
  width: 100%;
  height: 100%;
  border-radius: 10px 0 0 10px;
  background: #000;
`;

const ModalContent = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  line-height: 1.8;
  color: #141414;

  p {
    margin-bottom: 1rem;
    text-align: center;
    padding-left: 1rem;
    padding-right: 1rem;
  }

  button {
    padding: 10px 24px;
    // background: #141414;
    background: #6C63FF;
    border-radius: 10px;
    color: #fff;
    border: none;
  }
`;

const CloseModalButton = styled(MdClose)`
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 20px;
  width: 32px;
  height: 32px;
  padding: 0;
  z-index: 10;
`;

const metadataPrefix = 'https://gateway.pinata.cloud/ipfs/QmY6Dt2tu9THDQiHo4BkquwgvUZRgPYJBg9urvSNyBmaVo/';
const metadataExt = '.png';

export const BuyModal = ({ showModal, 
                        setShowModal, 
                        buy, 
                        isLoading, 
                        setIsLoading, 
                        nftImg, 
                        price, 
                        balanceError, 
                        setBalanceError,
                        spendApproved,
                        approveBalanceSpend }) => {

  const modalRef = useRef();

  const animation = useSpring({
    config: {
      duration: 250
    },
    opacity: showModal ? 1 : 0,
    transform: showModal ? `translateY(0%)` : `translateY(-100%)`
  });

  const closeModal = e => {
    if (modalRef.current === e.target) {
      setShowModal(false);
    }
  };

  const keyPress = useCallback(
    e => {
      if (e.key === 'Escape' && showModal) {
        setShowModal(false);
        console.log('I pressed');
      }
    },
    [setShowModal, showModal]
  );

  useEffect(
    () => {
      document.addEventListener('keydown', keyPress);
      return () => document.removeEventListener('keydown', keyPress);
    },
    [keyPress]
  );

  return (
    <>
      {showModal ? (
        <Background onClick={closeModal} ref={modalRef}>
          <animated.div style={animation}>
            <ModalWrapper showModal={showModal}>
              <Image
                preview={false}
                src={nftImg || "error"}
                fallback="data:image/png;base64,"
                alt=""
                style={{ width: '100px' }}
              />

              {
                spendApproved ? 
                      <ModalContent>
                        <h1>Buy This Invader</h1>
                        <p>Price: {price}</p>
                        {balanceError && <p style={{color: 'red'}}>Insufficient Balance!</p>}
                        <SpinnerDotted 
                          enabled={isLoading} 
                          color={'#6C63FF'} 
                          style={{marginBottom:'10px'}} 
                          size={35}
                          /> 
                        <button onClick={buy}
                                disabled={isLoading}
                                style={{background: isLoading ? '#B1B1B1' : '#6C63FF', cursor: isLoading ? 'default' : 'pointer'}}
                                >BUY NOW</button>
                        
                      </ModalContent>
                  :  <ModalContent>
                        <h1></h1>
                        <p>To approve space marketplace to transfer your $SPACE, click "Approve" below.</p>  
                        <SpinnerDotted 
                          enabled={isLoading} 
                          color={'#6C63FF'} 
                          style={{marginTop:'15px'}} 
                          size={35}
                          />         
                        {/* <button onClick={approveInvaders}>APPROVE</button> */}
                        <button onClick={approveBalanceSpend} 
                                disabled={isLoading}
                                style={{background: isLoading ? '#B1B1B1' : '#6C63FF', 
                                        cursor: isLoading ? 'default' : 'pointer',
                                        marginTop: '10px'
                                      }}
                                >
                                    APPROVE
                        </button>
                        
                    </ModalContent>
              }  

              


              <CloseModalButton
                aria-label='Close modal'
                onClick={() => {
                  setShowModal(prev => !prev);
                  setBalanceError(false);
                }}
              />
            </ModalWrapper>
          </animated.div>
        </Background> 
      ) : null}
    </>
  );
};